import React, {useState} from 'react'
import './navbar.css'
import {NavLink } from 'react-router-dom'
import poland from './poland.png'
import uk from './uk.png'
import ukraine from './ukraine.png'

const Navbar = ({setShowMain, setShowBooking, setShowPricing, setShowRules, setShowBigger, setShowSmaller, setShowStudio, setShowContact}) => {

    return (
        <div className="navbar">
            <div className="navbar-left">
            <NavLink to="/studio">
                <div onClick={()=>{
            setShowBooking(false)
            setShowMain(true)
            setShowPricing(false)
            setShowRules(false)
            setShowBigger(false)
            setShowSmaller(false)
            setShowStudio(false)
          }}>
            Próbówka Music House
        </div>
        </NavLink>
            </div>
            <div className="navbar-center">
                <NavLink 
                    to="/rezerwacje" 
                    activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}
                    >
                    <div className="nav-link" onClick={()=>{
                    setShowBooking(true)
                    setShowMain(false)
                    setShowPricing(false)
                    setShowRules(false)
                    setShowBigger(false)
                    setShowSmaller(false)
                    setShowStudio(false)
                    setShowContact(false)
                }}>Rezerwacje</div>
                </NavLink>
                <NavLink to="/sala_mniejsza" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <div className="nav-link" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(true)
                     setShowStudio(false)
                     setShowContact(false)
                }}>Sala 1</div>
                </NavLink>
                <NavLink to="/sala_wieksza" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <div className="nav-link" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(true)
                     setShowSmaller(false)
                     setShowStudio(false)
                     setShowContact(false)
                }}>Sala 2</div>
                </NavLink>
                <NavLink to="/studio_nagran" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <div className="nav-link" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(true)
                     setShowContact(false)
                }}>Studio</div>
                </NavLink>
                <NavLink to="/cennik" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <div className="nav-link" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(true)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(false)
                     setShowContact(false)
                }}>Cennik</div>
                </NavLink>
                <NavLink to="/regulamin" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <div className="nav-link" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(true)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(false)
                     setShowContact(false)
                }}>Regulamin</div>
                </NavLink>
                <NavLink to="/kontakt" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <div className="nav-link" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(false)
                     setShowContact(true)
                }}>Kontakt</div>
                </NavLink>
                
            </div>
            <div className="navbar-right">
                <div><img className="icon" src={poland}></img></div>
                <div><img className="icon" src={uk}></img></div>
                <div><img className="icon" src={ukraine}></img></div>
            </div>
        </div>
    )
}

export default Navbar
