import React, {useState, useEffect} from 'react'
import Header from './components/header/Header'
import MainContainer from './components/main/MainContainer'
import Booking from './components/booking/Booking'
import Footer from './components/footer/Footer'
import Rules from './components/rules/Rules'
import Pricing from './components/pricing/Pricing'
import Bigger from './components/bigger/Bigger'
import Smaller from './components/smaller/Smaller'
import Studio from './components/rehearsal/Studio'
import Contact from './components/contact/Contact'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'

const App = () => {

  const [showMain, setShowMain] = useState(true)
  const [showBooking, setShowBooking] = useState(false)
  const [showPricing, setShowPricing] = useState(false)
  const [showRules, setShowRules] = useState(false)
  const [showBigger, setShowBigger] = useState(false)
  const [showSmaller, setShowSmaller ] = useState(false)
  const [showStudio, setShowStudio] = useState(false)
  const [showContact, setShowContact] = useState(false)


  return (
    <Router>
    <div className="main">
      <Header 
      setShowMain={setShowMain} 
      setShowBooking={setShowBooking} 
      setShowPricing={setShowPricing} 
      setShowRules={setShowRules} 
      setShowSmaller={setShowSmaller}
      setShowBigger={setShowBigger}
      setShowStudio={setShowStudio}
      setShowContact={setShowContact}
      />
      <Switch>
        <Route exact path="/studio">
          {showMain &&
          <MainContainer/>
          }
        </Route>
        <Route exact path="/rezerwacje">
        {showBooking &&
          <Booking />
        }
        </Route>
        <Route exact path="/regulamin">
        {showRules &&
          <Rules/>
        }
        </Route>
        <Route exact path="/cennik">
        {showPricing &&
          <Pricing />
        }
        </Route>
        <Route exact path="/sala_mniejsza">
        {showSmaller &&
          <Smaller/>
        }
        </Route>
        <Route exact path="/sala_wieksza">
        {showBigger && 
          <Bigger/>
        }
        </Route>
        <Route exact path="/studio_nagran">
        {showStudio &&
          <Studio/>
        }
        </Route>
        <Route exact path="/kontakt">
          {showContact &&
          <Contact/>}
        </Route>
      </Switch>
      <Footer/>
    </div>
    </Router>
  )
}

export default App
