import React from 'react'
import Room from './Room'
import studioo from '../studioo.png';

const Stuff = () => {
    return (
        <div className="stuff-main">
                <h3>Sale prób</h3>
                <hr style={{width: '80px'}}></hr>
                <div className="stuff-content">
                    <Room image={studioo} title="Sala mniejsza" description="Sala mniejsza to mniejsza sala, w ktorej sala jest mniejsza"/>
                    <Room image={studioo} title="Sala większa" description="Sala większa to większa sala, w ktorej sala jest większa"/>
                    <Room image={studioo} title="Studio nagrań" description="Studio nagrań to studio nagrań, w ktorym jest studio nagrań"/>
                </div>
            </div>
    )
}

export default Stuff
